;((require('sheetify/insert')("@import url(\"https://fonts.googleapis.com/css?family=Lato|Lora\");\nbody {\n  font-family: 'Lora', Georgia, serif;\n  -webkit-font-smoothing: antialiased;\n  text-rendering: optimizeLegibility;\n  min-height: 100%;\n  margin: 0; }\n\na {\n  color: #41b7d8;\n  text-decoration: none;\n  border-bottom: 0.0625rem solid rgba(0, 0, 0, 0.1); }\n  a:hover {\n    border-bottom: 0.0625rem solid #41b7d8; }\n\nmain {\n  margin-left: auto;\n  margin-right: auto;\n  margin-bottom: 1rem; }\n  main.homepage {\n    width: 70%; }\n  main.article {\n    width: 80%; }\n\npre {\n  position: relative;\n  line-height: 0 !important;\n  border: 0.0625rem solid;\n  border-left: 0.375rem solid;\n  border-top-right-radius: 0.25rem;\n  border-bottom-right-radius: 0.25rem;\n  background-color: #f4f4f4 !important;\n  padding-left: 1rem;\n  border-color: rgba(0, 0, 0, 0.1);\n  margin-top: 0 !important; }\n  pre code {\n    font-size: 0.9375rem;\n    color: #777 !important;\n    display: block;\n    line-height: 1.5 !important;\n    transform: translateY(-2.5rem);\n    animation-duration: 1s;\n    animation-name: fadein; }\n    pre code .token.operator {\n      background: none !important; }\n\n@keyframes fadein {\n  from {\n    opacity: 0; }\n  to {\n    opacity: 1; } }\n\n.boowa-article {\n  display: block;\n  margin: 0 auto;\n  width: 80%; }\n  .boowa-article h1,\n  .boowa-article h2,\n  .boowa-article h3,\n  .boowa-article h4,\n  .boowa-article h5,\n  .boowa-article h6 {\n    font-family: 'Lato', Helvetica, Arial, sans-serif;\n    color: #4e4848;\n    -webkit-font-smoothing: antialiased; }\n  .boowa-article > header h1 {\n    text-align: center;\n    line-height: 1.5rem;\n    font-size: 2rem;\n    margin-top: 2.625rem;\n    margin-bottom: 3rem;\n    -webkit-text-stroke: 0.025rem; }\n  .boowa-article > header .gray {\n    color: gray; }\n  .boowa-article span {\n    font-size: 0.9rem; }\n  .boowa-article .reading-time {\n    float: right; }\n  .boowa-article .article {\n    margin-top: 2.5rem;\n    color: #4e4848; }\n    .boowa-article .article a {\n      border: none; }\n    .boowa-article .article ul li {\n      margin-bottom: 0.3125rem; }\n    .boowa-article .article code {\n      transform: none; }\n") || true) && "_c9bb1ec6")

class Component extends Nanocomponent {
  constructor () {
    super()
  }

  createElement (state, emit) {
    var article = state.params.article

    emit(state.events.DOMTITLECHANGE, titleObject[article])

    return html`
      <article class='boowa-article'>
        ${raw(eval(article))}
      </article>
    `
  }

  update () {
    return true
  }
}
module.exports = new Component()